<template>
	<!-- 主页面 -->
	<view id="root">
		<!-- 页面空格（调整间距） -->
		<view class="mar"></view>
		<!-- 登录页面 -->
		<view class="navtitle" v-if="yemian === 0">{{ title }}</view>
		<view v-if="yemian === 0" id="mainbody">
			<view class="form">
				<!-- 登录表单 -->
				<form @submit="onsubmit">
					<!-- 账号输入框 -->
					<view class="input_admin">
						<input type="text" placeholder="请输入账号或者手机号" v-model="adminn">
					</view>
					<!-- 密码输入框 -->
					<view class="input_password">
						<input type="password" placeholder="请输入密码" v-model="passwordd">
					</view>
					<!-- 登录按钮 -->
					<view class="butDL">
						<button form-type="submit" size="default" type="primary"
							style="height: 80rpx; line-height: 80rpx;">登录</button>
					</view>
					<view class="butJZ">
						<label>
							<checkbox :checked="jizhumima" @click="jiZhu" /><text>记住密码</text>
						</label>
					</view>
				</form>
				<!-- 切换到注册页面 -->
				<view class="butZC">
					<button form-type="submit" size="mini" class="zhucce" @click="handleRegister">注册</button>
				</view>
			</view>
		</view>
		<!-- 注册页面 -->
		<view v-if="yemian === 1">
			<!-- 返回到登陆页面 -->
			<view class="comeBackDengLu" @click="comeBackDL">
				<button @click="comeBackDL" size="mini" class="comeBackDengLuBut" style="">
					<返回登陆 </button>
			</view>
			<!-- 欢迎注册 -->
			<view class="huanyinzhuce">
				欢迎注册
			</view>
			<view>
				<!-- 注册表单 -->
				<form @submit="formSubmit" @reset="">
					<!-- 手机号 -->
					<view class="vinput">
						<image src="../../static/zhanghao.png" style="width: 40rpx;height: 40rpx;"></image>
						<view class="maohao">:</view> <input type="tel" focus="true" placeholder=" 账号"
							v-model="shoujihao">
					</view>
					<!-- 密码 -->
					<view class="vinput">
						<image src="../../static/password.png"></image>
						<view class="maohao">:</view> <input type="safe-password" placeholder=" 密码" v-model="mima">
					</view>
					<!-- 短信验证码 -->
					<view class="vinput" style="position: relative;">
						<image src="../../static/yanzhengma.png"></image>
						<view class="maohao">:</view> <input type="number" maxlength="6" placeholder=" 图片验证码"
							style="width: 200rpx;">
						<button size="mini" type="default" class="butyanzhengma" @click="getyzm"
							v-bind:disabled="btndisabled">{{yanzhengma}}</button>
					</view>
				</form>
				<!-- 提交注册按钮 -->
				<view class="butzhuce">
					<button form-type="submit" class="buttzhuce" plain="true" type="primary"
						@click="zhucezc">{{zhuce}}</button>
				</view>
				<!-- 其他方法 还没完善 -->
				<span></span>
				<image src="../../static/weChat.png" class="image1" @click="expect"></image>
				<image src="../../static/QQ.png" class="image2" @click="expect"></image>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		// 数据
		data() {
			return {
				title: "智能安防系统",
				yemian: 0,
				adminn: '',
				passwordd: '',
				yanzhengma: "获取验证码",
				zhuce: '注册',
				btndisabled: false,
				mima: "",
				shoujihao: "",
				yanzhengmayz: "",
				jizhumima: true
			};
		},
		// 函数
		methods: {
			// 计时函数
			timewait(t) {
				const _this = this;
				setTimeout(function() {
					if (t >= 0) {
						_this.yanzhengma = t + '秒后点击';
						t--;
						_this.timewait(t);
					} else {
						_this.yanzhengma = '获取验证码';
						t = 59;
						_this.btndisabled = false;
					}
				}, 1000)
			},
			// 获取验证码
			getyzm() {
				this.timewait(59);
				this.btndisabled = true;
			},
			// 切换页面
			handleRegister() {
				this.yemian = 1
			},
			// 登录提交
			onsubmit(e) {
				// 登录成功
				if (this.passwordd === '123456' && this.adminn === 'admin') {
					if (this.jizhumima) {
						console.log('记住密码');
						uni.setStorageSync('admin', this.adminn);
						uni.setStorageSync('password', this.passwordd);
					} else {
						console.log('不记住密码');
						uni.removeStorageSync('admin')
						uni.removeStorageSync('password')
						this.adminn = ''			
						this.passwordd = ''
					}
					uni.showLoading({
						title: '登陆中',
						success() {
							setTimeout(function() {
								uni.hideLoading()
								uni.switchTab({
									url: '/pages/list1/list1',
									success() {
										console.log('登录成功');
									}
								});
							}, 500);
						}
					})
					// 账号或者密码为空
				} else if (this.passwordd === '' || this.adminn === '') {
					uni.showModal({
						title: '请输入账号或者密码',
						showCancel: false,
					})
				}
				// 账号或者密码错误
				else {
					uni.showModal({
						title: '账号或者密码错误',
						showCancel: false,
					})
				}
			},
			jiZhu() {
				this.jizhumima = !this.jizhumima
			},
			// 注册提交
			formSubmit: function(e) {
				uni.request({
					url: "",
					data: {
						shoujihao: this.shoujihao,
						mima: this.mima
					},
					success: (res) => {
						this.yanzhengmayz = res.yanzhengma
					}
				})
			},
			// 返回登陆页面
			comeBackDL() {
				this.yemian = 0
				uni.navigateTo({
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			// 微信 QQ 登录敬请期待
			expect() {
				uni.showModal({
					title: "敬请期待",
					showCancel: false
				})
			}
		},
		mounted() {
			const username = uni.getStorageSync('adminn')
			const password = uni.getStorageSync('passowrdd')
		},
		onShow() {
			if (this.jizhumima) {
				console.log('记住密码');
				this.adminn = uni.getStorageSync('admin');
				this.passwordd = uni.getStorageSync('password')
			} else {
				console.log('不记住密码');
				uni.removeStorageSync('admin')
				uni.removeStorageSync('password')
				this.adminn = ''
				this.passwordd = ''
			}
		}
	}
</script>

<!-- SCC样式 -->
<style lang="scss" scoped>
	#root {
		height: 100vh;
		width: 100vw;
		background-size: cover;
		opacity: 1;
		background: url("../../static/beijing.jpg") no-repeat 100% 100%;
		background-color: #5F5F5F;

		.mar {
			width: 100vw;
			height: 240rpx;
		}

		.navtitle {
			font-size: 64rpx;
			text-align: center;
			color: #093543;
			text-shadow: 0rpx 0rpx 2rpx white;
		}

		#mainbody {
			opacity: 0.6;
			width: 600rpx;
			height: 460rpx;
			border: 1rpx #adb6bf solid;
			margin: 120rpx auto;
			text-align: center;
			border-radius: 24rpx;
			box-shadow: 0rpx 0rpx 24rpx gray;
			justify-content: space-between;
			background-color: #d3e6f1;

			.form {

				.input_password,
				.input_admin {
					height: 72rpx;
					width: 480rpx;
					border: 1rpx #045d75 solid;
					box-shadow: 0rpx 0rpx 4rpx black;
					margin: 48rpx auto;
					border-radius: 18rpx;
					line-height: 180rpx;
					font-size: 64rpx;

					input {
						height: 100%;
						font-size: 40rpx;
					}
				}

				.butDL {
					width: 320rpx;
					margin: 0rpx auto;
				}

				.butZC {
					width: 320rpx;
					margin: 20rpx auto;

					.zhucce {
						height: 40rpx;
						line-height: 40rpx;
						background-color: transparent;
						color: #888;
					}
				}

				.butJZ {
					float: right;
					margin-top: -124rpx;
					margin-right: 60rpx;
				}
			}
		}

		.comeBackDengLu {
			.comeBackDengLuBut {
				float: left;
				font-size: 36rpx;
				margin-top: -200rpx;
				background-color: transparent;
				color: #6473d6
			}
		}

		.huanyinzhuce {
			float: none;
			margin: 20rpx 0rpx 0rpx 240rpx;
			color: #154361;
			text-shadow: 0rpx 0rpx 4rpx white;
			height: 200rpx;
			width: 480rpx;
			font-size: 64rpx;
		}

		image {
			float: left;
			width: 40rpx;
			height: 40rpx;
		}

		.vinput {
			margin: 24rpx auto;
			height: 40rpx;
			width: 400rpx;
			border-bottom: 1rpx black solid;
			text-align: initial;

			.butyanzhengma {
				width: 120rpx;
				padding: 0;
				font-size: 18rpx;
				line-height: 24rpx;
				height: 30rpx;
				position: absolute;
				right: 0;
				top: 10rpx;
				text-align: center;
				text-shadow: 0rpx 0rpx 0rpx transparent;
			}

			.maohao {
				float: left;
				height: 40rpx;
				line-height: 40rpx;
			}
		}

		.butzhuce {
			width: 200rpx;
			height: 80rpx;
			margin: 40rpx auto;
			font-size: 12rpx;
			line-height: 80rpx;
		}

		.buttzhuce {
			height: 40rpx;
			line-height: 40rpx;
			font-size: 20rpx;
		}

		span {
			display: block;
			height: 64rpx;
			width: 200rpx;
			font-size: 24rpx;
			text-align: center;
			margin: 20rpx auto;
		}

		.image1 {
			margin-left: 220rpx;
			float: left;
			width: 80rpx;
			height: 80rpx;
			opacity: 0.4;
		}

		.image2 {
			opacity: 0.4;
			margin-right: 220rpx;
			float: right;
			width: 80rpx;
			height: 80rpx;
		}
	}
</style>